import React from 'react';

type State = {
  goods: string[];
};

type Props = {
  goods: string[];
};

export class GoodsList extends React.Component<Props, State> {
  state: State = {
    goods: this.props.goods,
  };

  defaultGoods = this.props.goods.slice();

  sortByAlp = () => {
    this.setState(state => ({
      goods: state.goods.sort(),
    }));
  };

  reverse = () => {
    this.setState(state => ({
      goods: state.goods.slice().reverse(),
    }));
  };

  reset = () => {
    this.setState({
      goods: this.defaultGoods,
    });
  };

  sortByLength = () => {
    this.setState(state => ({
      goods: state.goods.sort((a, b) => a.length - b.length),
    }));
  };

  render() {
    const {
      goods,
    } = this.state;

    return (
      <>
        <button type="button" onClick={this.reverse}>
          reverse
        </button>

        <button type="button" onClick={this.sortByAlp}>
          sort alphabitecaly
        </button>

        <button type="button" onClick={this.sortByLength}>
          sortByLength
        </button>

        <button type="button" onClick={this.reset}>
          reset
        </button>

        <ul>
          {goods.map(item => (
            <li key={item}>
              {item}
            </li>
          ))}
        </ul>
      </>
    );
  }
}
